import Layout from '../components/Layout';

export default () => (
	<Layout>
		<div>
			<div className="has-background-white">
				<br />
				<div className="columns">
					<div className="column"></div>
					<div className="column is-4">
						<p className="title has-text-link has-text-centered has-text-weight-normal">
							Get started with Xoyondo
						</p>
						<p className="subtitle has-text-centered has-text-weight-normal">
							It's free and takes only 12 seconds.
						</p>

						<div className="box" style={{ backgroundColor: '#C8E4FF' }}>
							<div className="field">
								<label className="label">Your name</label>
								<div className="control has-icons-left has-icons-right">
									<input className="input" type="text" placeholder="Your name" />
									<span className="icon is-small is-left">
										<i className="fas fa-signature"></i>
									</span>
								</div>
							</div>

							<div className="field">
								<label className="label">Your email</label>
								<div className="control has-icons-left has-icons-right">
									<input className="input" type="email" placeholder="Your email" />
									<span className="icon is-small is-left">
										<i className="fas fa-envelope"></i>
									</span>
								</div>
							</div>

							<div className="field">
								<label className="label">Password</label>
								<div className="control has-icons-left has-icons-right">
									<input className="input" type="password" placeholder="Your password" />
									<span className="icon is-small is-left">
										<i className="fas fa-key"></i>
									</span>
								</div>
							</div>

							<div className="field">
								<div className="control">
									<button className="button is-link is-fullwidth">Register</button>
								</div>
							</div>
						</div>
					</div>
					<div className="column"></div>
				</div>
				<br />
				<br />

				<div className="has-background-white-ter">
					<div className="container">
						<br />
						<br />
						<section className="section is-small">
							<div className="columns">
								<div className="column is-1"></div>
								<div className="column is-5">
									<br />
									<p className="is-size-3 has-text-dark">All your polls in one place</p>
									<p className="is-size-5 has-text-grey-light">
										With a free Xoyondo account you have all your polls in one place. The Dashboard
										offers direct access to your polls and lets you know about recent activities.
									</p>
								</div>
								<div className="column has-text-centered">
									<img src="https://xoyondo.com/img/Dashboard-en.png" alt="" />
								</div>
							</div>
						</section>
					</div>
				</div>
			</div>
		</div>
	</Layout>
);
